AA;Ed8kBF;Ec5kBA;IACI,mCAAA;Ed8kBJ;Ec3kBA;IACI,yBTKuB;ISJvB,cTMsB;ISLtB,YAAA;IACA,gBAAA;IACA,6BT9BO;EL2mBX;Ec3kBA;IACI,mBTDmB;ISEnB,cTLqB;ELklBzB;EczkBA;IACE,aAAA;Ed2kBF;EczkBA;IACE,yBT2BK;IS1BL,mBAAA;IACA,iBAAA;IACA,oBAAA;IACA,8CAAA;IACA,mBAAA;Ed2kBF;EczkBA;IACE,YAAA;Ed2kBF;AA61DF;;AyB/9EA;EJAA;IAyBQ,iBAAA;ErB29CN;AAi/BF;;AyBr+EA;EHAA;IAKQ,iBAAA;EtBqgDN;EsBhgDF;IAIQ,gBAAA;EtBqgDN;EwB17CF;IAaI,gBAAA;IACA,iBAAA;ExB02EF;AAiCF;;AyBl/EA;EP2BA;IAqBQ,SAAA;ElBm6BN;AAqiDF","sourcesContent":["\n.container {\n  width: 100%;\n  margin: 0 auto;\n\n  padding-top: 0;\n  padding-bottom: 0;\n  padding-left:  clamp(12px, 0.27rem + 1.84vw, 32px);\n  padding-right: clamp(12px, 0.27rem + 1.84vw, 32px);\n\n  max-width: 1440px;\n\n  // max-width: clamp(352px, -0.617rem + 97vw, 1376px)\n\n  // @include mobile {\n  //   max-width: 376px;\n  // }\n\n  // @include tablet {\n  //   max-width: 768px;\n  // }\n\n  // @include desktop {\n  //   padding: 0 32px;\n  //   max-width: 1440px;\n  // }\n}","@import '~normalize.css';\n\n// utils\n@import '../../style/utils/mixin';\n@import '../../style/utils/container';\n@import '../../style/utils/fonts';\n@import '../../style/utils/is-hidden';\n@import '../../style/utils/reset';\n@import '../../style/utils/vars';\n@import '../../style/common';\n\n// component\n@import '../../style/components/index.scss';\n\n// layout\n@import '../../style/header';\n@import '../../style/footer';\n\n// modals\n@import '../../style/modals/burger-modal';\n@import '../../style/modals/pop-up-modal';\n\n//calculator \n@import '../../style/react/calculator/calculator.scss';\n\n\n// partials\n@import '../../style/hero';\n@import '../../style/our-pref-step-cards';\n@import '../../style/last-works';\n\n\n// swiper\n@import 'swiper/css/bundle';\n@import '../../style/swiper'\n\n\n","@font-face {\n  font-family: \"M PLUS 1p\";\n  src: url('../../fonts/MPLUS1p-Regular.woff2') format('woff2'),\n       url('../../fonts/MPLUS1p-Regular.woff') format('woff'),\n       url('../../fonts/MPLUS1p-Regular.ttf') format('truetype');\n  font-weight: 400;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: \"M PLUS 1p\";\n  src: url('../../fonts/MPLUS1p-Medium.woff2') format('woff2'),\n       url('../../fonts/MPLUS1p-Medium.woff') format('woff'),\n       url('../../fonts/MPLUS1p-Medium.ttf') format('truetype');\n  font-weight: 500;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: \"Russo One\";\n  src: url('../../fonts/Russo_One.woff2') format('woff2'),\n       url('../../fonts/Russo_One.woff') format('woff'),\n       url('../../fonts/Russo_One.ttf') format('truetype');\n  font-weight: 400;\n  font-display: swap;\n}\n\n  // @font-face {\n  //   font-family: 'Roboto';\n  //   src: url('../../fonts/Roboto-Regular.woff') format('woff'),\n  //     url('../../fonts/Roboto-Regular.woff2') format('woff2');\n  //   font-weight: 400;\n  // }",".is-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    margin: -1px;\n    border: 0;\n    padding: 0;\n\n    white-space: nowrap;\n    clip-path: inset(100%);\n    clip: rect(0 0 0 0);\n    overflow: hidden;\n}","*{\n    box-sizing: border-box;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\nimg {\n    display: block;\n    max-width: 100%;\n    height: auto;\n}\n\na {\n    text-decoration: none;\n    color: currentColor;\n    display: block;\n}\n\nul {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n.link {\n    text-decoration: none;\n}\n\n.list {\n    text-decoration: none;\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\nsvg{\n    fill: currentColor;\n}\n","html,\nbody {\n  font-family: $main;\n  background-color: $main-bg;\n\n  font-size: clamp(18px, 0.5rem + 1.2vw, 20px);\n  font-weight: 400;\n  line-height: clamp(24px, 0.8rem + 1.5vw, 28px);\n  color: $main-text;\n  \n  margin: 0 auto;\n  width: 100%;\n\n  scroll-behavior: smooth;\n}\n//   clamp(min, current, max)\n//   window 1440px / max font-size = 44px\n//   1vw = 14.4px\n//   44px / 14.4px = 3.05vw\nbody.hidden{\n  overflow: hidden;\n}\n\naddress {\n  font-style: normal;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\n// link hover focus active \n.link-hfa{\n  transition: $transition;\n\n  &:hover,&:focus{\n    color: $hover-accent-text;\n    & svg{\n      transition: $transition;\n      color: $hover-accent-text;\n    }\n  }\n  &:active{\n    color: $active-accent-text;\n\n    & svg{\n      transition: $transition;\n      color: $active-accent-text;\n    }\n  }\n}\n\n\n.bg-card{\n  background-color: $card-bg;  \n}\n\n.br-16-to-24{\n  border-radius: clamp(16px, 0.6rem + 1.1vw, 24px);\n}\n\n.br-20{\n  border-radius: 20px;\n}\n\n.br-24{\n  border-radius: 24px;\n}\n\n.br-24-to-20{\n  border-radius: clamp(20px, 1.7rem - 1.5vw, 24px);\n}\n\n// width height \n.w-32-to-48{\n  width: clamp(32px, 1rem + 4vw, 48px);\n}\n.h-32-to-48{\n  height: clamp(32px, 1rem + 4vw, 48px);\n}\n\n\n\n// padding margin\n.p-lr-12-to-32{\n  padding-left: clamp(12px, 0.125rem + 2.3vw, 32px);\n  padding-right: clamp(12px, 0.125rem + 2.3vw, 32px);\n}\n.p-lr-12-to-56{\n    padding-left: clamp(12px, -0.4rem + 5vw, 56px);\n    padding-right: clamp(12px, -0.4rem + 5vw, 56px);\n}\n\n.p-tb-12-to-16{\n  padding-top: clamp(12px, 0.625rem + 0.5vw, 16px);\n  padding-bottom: clamp(12px, 0.625rem + 0.5vw, 16px);\n}\n.p-tb-16-to-32{\n  padding-top: clamp(16px, 0.4rem + 1.9vw, 32px);\n  padding-bottom: clamp(16px, 0.4rem + 1.9vw, 32px);\n}\n.p-tb-32-to-56{\n  padding-top: clamp(32px, 0.4rem + 6.52vw, 56px);\n  padding-bottom: clamp(32px, 0.4rem + 6.52vw, 56px);\n}\n\n.mb-24{\n  margin-bottom: 24px;\n}\n\n.mb-24-to-32{\n  margin-bottom: clamp(24px, 1rem + 1vw, 32px);;\n}\n\n.mb-16-to-24 {\n  margin-bottom: clamp(16px, 0.75rem + 1vw, 24px);\n}\n.mb-40-to-32{\n  margin-bottom: clamp(40px, 1.25rem + 1vw, 32px);\n}\n.mb-44-to-68{\n  margin-bottom: clamp(44px, 2rem + 3vw, 68px);\n}\n.mb-88-to-120{\n  margin-bottom: clamp(88px, 3.5rem + 5vw, 120px);\n}\n.mt-24-to-32{\n  margin-top: clamp(24px, 1.25rem + 1vw, 32px);;\n}\n.ml-24{\n  margin-left: 24px;\n}\n\n\n// flex\n.flex {\n  display: flex;\n}\n.fd-row{\n  flex-direction: row;\n}\n.fd-col{\n  flex-direction: column;\n}\n.wrap{\n  flex-wrap: wrap;\n}\n.jc-sb{\n  justify-content: space-between;\n}\n.ai-c{\n  align-items: center;\n}\n.gap-12-to-32{\n  gap: clamp(12px, 0.125rem + 2.5vw, 32px)\n}\n.gap-16-to-24{\n  gap: clamp(16px, 0.394rem + 2.5vw, 24px);\n}\n.gap-24-to-32{\n  gap: clamp(24px, 1rem + 1vw, 32px);\n}\n.gap-44-to-68{\n  gap: clamp(44px, 2rem + 4vw, 68px)\n}\n.gap-88-to-120{\n  gap: clamp(88px, 3.5rem + 5vw, 120px);\n}\n\n// error\n.error-message{\n  position: absolute;\n  bottom: -28px;\n  left: 24px;\n\n  font-size: 12px;\n  line-height: 16px;\n  color: $error-text;\n  opacity: 0;\n\n  margin-top: 12px;\n\n  transition: $transition;\n}\n.error-message.error{\n  opacity: 1;\n}\n\n\n\n\n\n\n\n\n\n","// font family\n$main:\"M PLUS 1p\", sans-serif;\n$russo:\"Russo One\", sans-serif;\n\n// transition\n$transition: all 0.3s ease-out;\n$transition-burger: all 0.5s ease-out;\n\n// text\n$black:#000;\n$white:#fff;\n$white-text: #F6F7F7;\n$main-text:#C2CDC6;\n$error-text:#DD7A02;\n\n$hover-accent-text: #F9A207;\n$active-accent-text: #FFD74A;\n\n$input-select-text:#171C1A;\n$about-us-subheader-text:#FFC935;\n$input-label-text:#E1E6E3;\n$input-placeholder-text:#73877B;\n\n\n\n// buttons\n$btn-primary-text:#171C1A;\n$btn-primary-bg:#FFC935;\n$btn-primary-accent-bg:#F9A207;\n$btn-primary-active-bg:#FFD74A;\n$btn-primary-disable-bg:#333C38;\n\n\n$btn-secondary-text:#F6F7F7;\n$btn-secondary-accent-text:#171C1A;\n$btn-secondary-border:1px solid $white-text;\n$btn-secondary-accent-border:1px solid #FFD74A;\n$btn-secondary-accent-bg:#FFD74A;\n$btn-secondary-desktop-text:#FFC935;\n$btn-secondary-desktop-border:1px solid #FFC935;\n\n$btn-pop-up-close: #73877B;\n\n// tabs \n// $tab-bg:#5C7